import { format } from '../../../utils'

export default {
  data () {
    return {
      head_icon: 'http://iph.href.lu/200x200?text=%E9%BB%98%E8%AE%A4%E5%A4%B4%E5%83%8F'
    }
  },
  props: ['message'],
  computed: {
    date () {
      const date = new Date(this.message.date)
      const now = new Date()
      const diff = now - date
      if (diff < 2 * 60 * 1000) return
      const day = 24 * 60 * 60 * 1000
      let time
      if (diff < day && date.getDate() === now.getDate()) { // 
        time = format(date, 'hh:mm')
      } else if (diff > day && diff < 2 * day && date.getDate() === now.getDate() + 1) {  // 
        time = ''
      } else if (diff > 2 * day) {
        time = format(date, 'MMdd')
      }
      return time
    },
    reverse () {
      const username = sessionStorage.getItem('username')
      return this.message.from.username === username
    }
  }
}
