import { ViewBox, XHeader, Group, Cell, XButton } from 'vux'

export default {
  name: 'detail',
  data () {
    return {
      user: {}
    }
  },
  methods: {
    click () {
      if (this.user.friendly) {
        // chat
        this.$router.replace({
          name: 'chat',
          query: {
            mode: 'push',
            title: this.user.username
          }
        })
      } else {
        this.$addFriend(this.user.username, err => {
          if (err) {
            console.log(err)
            return
          }

          // 
          console.log('')
          this.$completed('')
        })
      }
    }
  },
  mounted () {
    // 
    const username = this.$route.query.username
    this.$search(username, (err, user) => {
      if (err) {
        console.log(err)
        return
      }

      this.user = user
    })
  },
  components: {
    ViewBox,
    XHeader,
    Group,
    Cell,
    XButton
  }
}
